.content{
    display: grid;
    grid-template-areas:
            "i u"
            "b b";
    grid-template-columns: min-content min-content;
}
.input{
    grid-area: i;
}
.addButton{
    grid-area: b;
}
.userCount {
    grid-area: u;
    margin: 30px 0 0 5px;
    font-weight: bolder;
}

